*{ margin: 0;padding: 0; -webkit-box-sizing: border-box;box-sizing: border-box; }

html{ -ms-text-size-adjust: 100%;-webkit-text-size-adjust: 100%; }

article, aside, details, figcaption, figure, footer, header, main, nav, section, summary{ display:block; }

audio, canvas, progress, video{ display: inline-block;vertical-align: baseline; }

audio:not([controls]){ display: none; height: 0; }

[hidden], template{ display: none; }

svg:not(:root){ overflow: hidden; }

a{ background: transparent; -webkit-tap-highlight-color: transparent;}

a, a:link, a:active, a:hover, a:visited{ color: #999;text-decoration: none; }

a:active{ outline: none; }

abbr[title]{ border-bottom: 1px dotted; }

b, strong{ font-weight:bold; }

dfn{ font-style: italic; }

mark{ background: #ff0; color: #000; }

small{ font-size: 80%; }

sub, sup{ font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sup{ top: -0.5em; }

sub{ bottom: -0.25em; }

img{ border: 0;vertical-align: middle; }

hr{ -webkit-box-sizing: content-box; box-sizing: content-box; height: 0; }

pre{ overflow: auto; white-space: pre; white-space: pre-wrap; word-wrap: break-word; }

code, kbd, pre, samp { font-family: monospace, monospace; }

button, input, optgroup, select, textarea{ color: inherit; font: inherit; margin: 0; }

button { overflow: visible; }

button, select { text-transform: none; }

button, html input[type="button"], input[type="reset"], input[type="submit"]{ -webkit-appearance: button; cursor: pointer; }

button[disabled], html input[disabled] { cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner{ border: 0; padding: 0; }

input { line-height: normal;border: none;outline-style: none; }

input[type="checkbox"], input[type="radio"]{ -webkit-box-sizing: border-box; box-sizing: border-box; padding: 0; }

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button { height: auto; }

input[type="search"]{ -webkit-appearance: textfield; -webkit-box-sizing: border-box; box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration { -webkit-appearance: none; }

input::-webkit-input-placeholder,input:-moz-placeholder,input::-moz-placeholder,input:-ms-input-placeholder { color: #D0D0D0;}

fieldset{ border: 1px solid #e5e5e5; }

legend{ border: 0; padding: 0; }

textarea{ overflow: auto; resize: vertical; }

optgroup{ font-weight: bold; }

table{ border-collapse: collapse; border-spacing: 0; }

td, th{ padding: 0; }

ul,ol,li{ list-style-type: none; }

.clearfix{ display: block; clear: both; zoom: 1;}

.clearfix:after{ display: block;content: "";clear: both;height: 0;line-height: 0;overflow: hidden;visibility: hidden; }

.left{ float: left; }

.right{ float: right; }

html { font-size: 10px;font-size: 2.6667vw; }

/*H5PC*/

@media (min-width: 750px) {
  html {
    font-size: 20px;
  }
}

/*4.4*/

@media (device-width: 320px) {
  html {
    font-size: 8.5333px;
  }
}

@media (device-width: 360px) {
  html {
    font-size: 9.6px;
  }
}

@media (device-width: 375px) {
  html {
    font-size: 10px;
  }
}

@media (device-width: 384px) {
  html {
    font-size: 10.24px;
  }
}

@media (device-width: 412px) {
  html {
    font-size: 10.9867px;
  }
}

@media (device-width: 414px) {
  html {
    font-size: 11.04px;
  }
}

@media (device-width: 480px) {
  html {
    font-size: 12.8px;
  }
}

body { background: #f6f6f6; }

body .component-wrap { max-width: 37.5rem; padding: 0; margin: 0 auto;  }

.oh-1 { white-space: nowrap;overflow: hidden;text-overflow: ellipsis; }

.oh-2 { display: -webkit-box;overflow: hidden;text-overflow: ellipsis;-webkit-line-clamp: 2;-webkit-box-orient: vertical; }

