  background: #fff;
  -webkit-box-shadow: 0 0.2rem 0.9rem 0 rgba(0,0,0,0.10);
  -ms-box-shadow: 0 0.2rem 0.9rem 0 rgba(0,0,0,0.10);
  -o-box-shadow: 0 0.2rem 0.9rem 0 rgba(0,0,0,0.10);
  box-shadow: 0 0.2rem 0.9rem 0 rgba(0,0,0,0.10);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  z-index: 2;
}

.list-tab li{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 4.8rem;
  line-height: 4.8rem;
  text-align: center;
  font-size: 1.5rem;
}

.list-tab li .hover {
  padding: 0.8rem 0;
  border-bottom: 0.3rem solid #494AFF;
  font-weight: bold;
}

.list-tab li .tab-not {
  color: #F22044;
}

.list-tab li .tab-done {
  color: #1CBE20;
}

